import { NgModule } from '@angular/core';
import { Routes, RouterModule,ExtraOptions } from '@angular/router';
import { RegistrationComponent } from './registration/registration.component';
import { LoginComponent } from './login/login.component';
import { HomeComponent } from './home/home.component';
import { LogoutComponent } from './logout/logout.component';
import { PrescriptionComponent } from './prescription/prescription.component';
import { DiseaseComponent } from './disease/disease.component';
import { ViewComponent } from './view/view.component';
import { SerachDeleteComponent } from './serach-delete/serach-delete.component';
import { PricingComponent } from './pricing/pricing.component';

const routes: Routes = [
  {path:"register",redirectTo:"register",pathMatch:"full"},
  {path:"login",redirectTo:"login",pathMatch:"full"},
  {path:"logout",redirectTo:"logout",pathMatch:"full"},
  {path:"",redirectTo:"home",pathMatch:"full"},
  {path:"prescription",redirectTo:"prescription",pathMatch:"full"},
  {path:"view",redirectTo:"view",pathMatch:"full"},
  {path:"disease",redirectTo:"disease",pathMatch:"full"},
  {path:"pricing",redirectTo:"pricing",pathMatch:"full"},
   {path:"register",component:RegistrationComponent},
   {path:"search",component:SerachDeleteComponent},
   {path:"login",component:LoginComponent},
   {path:"home",component:HomeComponent},
   {path:"logout",component:LogoutComponent},
   {path:"prescription",component:PrescriptionComponent},
   {path:"view",component:ViewComponent},
   {path:"disease",component:DiseaseComponent},
   {path:"pricing",component:PricingComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    scrollPositionRestoration: 'enabled',
    anchorScrolling: 'enabled',
  })],
  exports: [RouterModule]
})

export class AppRoutingModule { }
